import React from 'react';

const screen = {
  mobileMax: 767,
  tabletMax: 991,
};

export default ({ className = '', children, ...props }) => (
  <div className={`container ${className}`} {...props}>
    {children}
    <style jsx>{`
      .container {
        margin: 0 auto;
        padding: 0 50px;
        max-width: 1280px;
      }

      @media (max-width: ${screen.tabletMax}px) {
        .container {
          padding: 0 40px;
        }
      }

      @media (max-width: ${screen.mobileMax}px) {
        .container {
          padding: 0 24px;
        }
      }
    `}</style>
  </div>
);
