import React from 'react';
import { colors, fonts } from '../constants';
import RightArrowIcon from './icons/RightArrow';

export default ({
  className = '',
  children,
  large,
  hasArrow,
  primary,
  ...props
}) => (
  <button className={`button ${className}`} {...props}>
    {children}
    {hasArrow && (
      <span className="button__arrow-icon">
        <RightArrowIcon
          color={primary ? colors.secondary : colors.primary}
          backgroundColor={primary ? colors.primary : colors.secondary}
        />
      </span>
    )}
    <style jsx>{`
      .button {
        font-size: ${large ? '19' : '18'}px;
        font-weight: 600;
        font-family: ${fonts.gilroy};
        color: ${primary ? colors.secondary : colors.primary};
        background-color: ${primary ? colors.primary : colors.secondary};

        height: ${large ? '58' : '50'}px;
        line-height: ${large ? '58' : '50'}px;
        padding: 0 28px;
        border: 0;
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.25351);
      }

      .button:hover:enabled {
        cursor: pointer;
        background-color: ${primary ? 'rgba(31, 50, 93, 0.95)' : '#fafafa;'};
      }

      .button:disabled {
        background-color: ${primary ? 'rgba(31, 50, 93, 0.5)' : '#ccc'};
      }
      .button__arrow-icon {
        position: relative;
        top: 10px;
        right: -10px;
      }
    `}</style>
  </button>
);
